@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}

























.title-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: $titleBarHeight;
  padding: 0 $md;
  background: $white;
  z-index: $zTitleBar;
  box-shadow: 0px 2px 2px 2px $graySuperLight;

  .app-title {
    color: $textColorMain;
    text-decoration: none;
  }

  .time-logo {
    margin-right: $sm;
  }

  .wwt-logo,
  .time-logo {
    height: 29px;
  }

  .wwt-logo,
  .time-logo,
  .app-name {
    vertical-align: middle;
  }

  .app-name {
    color: $grayMid;
    font-size: 18px;
  }
}
