@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}





































































.hours-grid {
  display: grid;
  grid-template-rows: 1fr auto;
  grid-template-columns: 1fr;
  text-align: left;
}

.resource-grid {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr;

  .burn-rate {
    grid-column-start: 2;
    grid-column-end: 8;
  }
}

.hours-grid-header {
  grid-row-start: 1;
  border-bottom: 1px solid $grayVeryLight;
}

.resource-hour,
.hours-grid-header-col.day-of-week,
.resource-total,
.hours-grid-header-col.total {
  border-left: 1px solid $grayVeryLight;
  text-align: center;
}

.resource-grid-row,
.hours-grid-header {
  width: 100%;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 2fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 1fr 1fr;
}

.resource-grid {
  padding: $lg 0;
  border-bottom: 1px solid $grayVeryLight;

  &-row:first-child {
    margin-bottom: $lg;
  }
}

.hours-grid-header-col,
.resource-grid-col {
  display: inline-block;
  padding: $sm;
  align-self: center;
}

.hours-grid-body {
  grid-row-start: 2
}
