@import 'styles/variables';
@import 'styles/mixins';

@import "styles/utils";
@import 'styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}





















@font-face {
  font-family: 'Proxima Nova';
  src: url('./assets/fonts/proximanova-light-webfont.eot');
  src: url('./assets/fonts/proximanova-light-webfont.eot?#iefix') format('embedded-opentype'),
  url('./assets/fonts/proximanova-light-webfont.woff') format('woff'),
  url('./assets/fonts/proximanova-light-webfont.ttf') format('truetype'),
  url('./assets/fonts/proximanova-light-webfont.svg#proxima_novalight') format('svg');
