@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}















.pmr-btn {
  color: $white;
  border: 1px solid transparent;
  display: inline-block;
  position: relative;
  border-radius: 0;
  padding: 0.375em .75em;
  font: inherit;
  line-height: $inputLineHeight;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
  background-color: transparent;
  cursor: pointer;

  &::selection {
    background: transparent;
  }

  &.sm {
    font-size: .875rem;
    padding: $xs .75rem;
  }

  &.md {
    font-size: $md;
  }

  &.lg {
    font-size: 1.25rem;
    padding: .75rem 1.25rem;
    line-height: 1.33333;
  }

  &.default {
    @include button-color($grayLight);

    &.dismiss {
    @include dismiss-button($grayLight);
    }
  }

  &.primary {
    @include button-color($primaryColor);

    &.dismiss {
      @include dismiss-button($primaryColor);
    }
  }

  &.success {
    @include button-color($successColor);

    &.dismiss {
      @include dismiss-button($successColor);
    }
  }

  &.warning {
    @include button-color($warningColor);

    &.dismiss {
      @include dismiss-button($warningColor);
    }
  }

  &.error {
    @include button-color($errorColor);

    &.dismiss {
      @include dismiss-button($errorColor);
    }
  }

  &.full {
    display: block;
    width: 100%;
  }
}
