@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';


html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  background-color: $nearWhite;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}































.burn-rate,
.progress-text {
  display: flex;
}

.progress-text {
  flex: 0 0 auto;
  margin-left: $lg;
}

.progress-bar {
  height: 1rem;
  width: 100%;
  min-width: 5rem;
  display: inline-block;
  position: relative;
  flex: 1 1 auto;

  &-fill {
    background-color: $blue;
    display: inline-block;
    width: 0%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    border-radius: .5rem;
  }

  &-background {
    background-color: $graySuperLight;
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: .5rem;
  }
}
